/* -------------------------------------------------------------------------------------------------
  # Custom Normalize
------------------------------------------------------------------------------------------------- */
menu,
nav,
section {
  display: block;
}

[hidden] {
  display: none;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:active, a:hover {
  outline-width: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

button {
  font: inherit;
  margin: 0;
  overflow: visible;
  text-transform: none;
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

button,
html [type="button"] {
  -webkit-appearance: button;
}

/* -------------------------------------------------------------------------------------------------
  # Panel
------------------------------------------------------------------------------------------------- */
.cm-panel {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: #efefef;
  opacity: 1;
  visibility: hidden;
  transition: all 0.25s ease-in-out;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
}

.cm-panel-top {
  transform: translateY(-100%);
}

.cm-panel-bottom {
  transform: translateY(100%);
}

.cm-panel-left {
  transform: translateX(-100%);
}

.cm-panel-right {
  transform: translateX(100%);
}

.cm-panel-fade {
  opacity: 0;
}

.cm-panel-open {
  visibility: visible;
  transform: translateY(0);
  opacity: 1;
}

/* -------------------------------------------------------------------------------------------------
  # Buttons
------------------------------------------------------------------------------------------------- */
.cm-hamburger {
  position: relative;
  z-index: 100;
  border: none;
  width: 60px;
  height: 45px;
  transform: rotate(0deg);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
  background: white;
}

.cm-hamburger span {
  display: block;
  position: absolute;
  height: 9px;
  width: 100%;
  background: black;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: all 0.25s ease-in-out;
}

.cm-hamburger span:nth-child(1) {
  top: 0px;
  transform-origin: left center;
}

.cm-hamburger span:nth-child(2) {
  top: 18px;
  transform-origin: left center;
}

.cm-hamburger span:nth-child(3) {
  top: 36px;
  transform-origin: left center;
}

.cm-hamburger-open span:nth-child(1) {
  transform: rotate(45deg);
  top: -3px;
  left: 8px;
}

.cm-hamburger-open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.cm-hamburger-open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 39px;
  left: 8px;
}

/* -------------------------------------------------------------------------------------------------
  # Menu(s)
------------------------------------------------------------------------------------------------- */
.cm-panel > ul {
  margin: 0 auto;
  padding: 0;
  position: relative;
  top: 20%;
  width: 90%;
}

.cm-panel > ul li {
  position: relative;
}

.cm-panel > ul li > [class*="icon-"] {
  position: absolute;
  top: 0;
  right: 0;
}

.cm-panel > ul li > ul {
  margin: 0;
  padding: 0;
  display: none;
}

.cm-panel > ul li > ul a {
  position: relative;
  font-size: 1rem;
  padding: 0.5em 2em;
}

.cm-panel > ul a {
  position: relative;
  display: block;
  padding: 0.25em 0;
  overflow: hidden;
  text-decoration: none;
}
